.result {
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  &-title {
    font-size: 3rem;
    text-transform: uppercase;
    margin-bottom: 1rem;
  }
}

.link {
  cursor: pointer;
  &:hover {
    color: #ffc93c;
  }
}
